





















































import {
	mapState
} from 'vuex';
import {
	mapMutations
} from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			total: 0, //总价格
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: [],
		};
	},
	onShow() {
		if (this.hasLogin) {
			this.loadData();
		}
	},
	onHide() { //离开页面清除数据
		this.cartList = [];
	},
	watch: {
		//显示空白页
		cartList(e) {
			let empty = e.length === 0 ? true : false;
			if (this.empty !== empty) {
				this.empty = empty;
			}
		}
	},
	computed: {
		...mapState(['hasLogin'])
	},
	methods: {
		...mapMutations(['saveWantBuy']),
		//请求数据
		loadData() {
			this.getCartList();
		},
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin() {
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		//选中状态处理
		check(type, index) {
			if (type === 'item') {
				this.cartList[index].checked = !this.cartList[index].checked;
			} else {
				const checked = !this.allChecked
				const list = this.cartList;
				list.forEach(item => {
					item.checked = checked;
				})
				this.allChecked = checked;
			}
			this.calcTotal(type);
		},
		//数量
		numberChange(data) {
			this.cartList[data.index].number = data.number;
			this.calcTotal();
			this.updateCart({
				id: this.cartList[data.index].id,
				number: this.cartList[data.index].number
			});
		},
		//删除
		deleteCartItem(index) {
			let list = this.cartList;
			let row = list[index];
			let id = row.id;
			this.calcTotal();

			//发起删除购物车请求
			this.deleteCart({
				ids: id
			}, () => {
				this.cartList.splice(index, 1);
			});
		},
		//清空
		clearCart() {
			uni.showModal({
				content: '清空购物车？',
				success: (e) => {
					if (e.confirm) {
						//发起清空购物车请求
						let ids = [];
						for (let i in this.cartList) {
							ids.push(this.cartList[i].id)
						}

						this.deleteCart({
							ids: ids.join(',')
						});
						this.cartList = [];
					}
				}
			})
		},
		//计算总价
		calcTotal() {
			let list = this.cartList;
			if (list.length === 0) {
				this.empty = true;
				return;
			}
			let total = 0;
			let checked = true;
			list.forEach(item => {
				if (item.checked === true) {
					total += item.commosity_sku.sku_price * item.number;
				} else if (checked === true) {
					checked = false;
				}
			})
			this.allChecked = checked;
			this.total = Number(total.toFixed(2));
		},
		//跳转到确认订单页面
		createOrder() {
			let list = this.cartList;
			let sendData = [];
			list.forEach(item => {
				if (item.checked) {
					sendData.push({
						sku_id: item.commosity_sku.id,
						number: item.number
					})
				}
			})
			this.saveWantBuy(sendData);
			uni.navigateTo({
				url: '/pages/sureorder/sureorder?wantBuy=1'
			});
		},
		// ajax

		//购物车列表
		getCartList: function() { //获取商品列表
			this.$load.loadding('加载中……');

			let token = this.$ajax.getToken();
			uni.request({
				method: 'GET',
				url: this.$ajax.ajaxBaseUrl + '/api/cart',
				dataType: 'json',
				header: {
					'content-type': 'application/json',
					"Authorization": token.token_type + ' ' + token.access_token
				},
				success: (res) => {
					this.$load.endLoadding();
					if (res.data.status === 0) {
						let cartList = res.data.data.map(item => {
							item.checked = true;
							return item;
						});
						this.cartList = cartList;
						this.calcTotal(); //计算总价
					} else {
						this.$api.msg(res.data.message);
					}
				}
			});
		},
		//更新购物车
		updateCart: function(option = {}) {
			let token = this.$ajax.getToken();
			let data = {};
			for (let i in option) {
				data[i] = option[i]
			}
			if (!data.id) {
				return;
			}

			uni.request({
				method: 'PUT',
				url: this.$ajax.ajaxBaseUrl + '/api/cart/' + data.id,
				dataType: 'json',
				data: data,
				header: {
					'content-type': 'application/json',
					"Authorization": token.token_type + ' ' + token.access_token
				},
				success: (res) => {
					if (res.data.status === 0) {
						console.log(res);
					} else {
						this.$api.msg(res.data.message);
					}
				}
			});
		},
		//删除购物车中部分商品
		deleteCart: function(option = {}, successCallBack = () => {}) {
			let token = this.$ajax.getToken();
			let data = {};
			for (let i in option) {
				data[i] = option[i]
			}
			if (!data.ids) {
				return;
			}
			uni.request({
				method: 'DELETE',
				url: this.$ajax.ajaxBaseUrl + '/api/cart/' + data.ids,
				dataType: 'json',
				data: data,
				header: {
					'content-type': 'application/json',
					"Authorization": token.token_type + ' ' + token.access_token
				},
				success: (res) => {
					if (res.data.status === 0) {
						successCallBack();
					} else {
						this.$api.msg(res.data.message);
					}
				}
			});
		}
	}
}
